import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { MenuController, Platform } from '@ionic/angular';
import { forkJoin, Subscription } from 'rxjs';

import { Camera, CameraResultType, CameraSource, Photo } from '@capacitor/camera';
import { Directory, Filesystem } from '@capacitor/filesystem';

import { MenuItem, MessageService } from 'primeng/api';

import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { UserDTO } from 'src/app/shared/interfaces/users/UserDTO';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { PreferencesService } from 'src/app/shared/services/preferences/preferences.service';
import { TransacaoService } from 'src/app/shared/services/transacao/transacao.service';
import { IMAGE_DIR } from 'src/config/app.config';
import { LocalFileDTO } from 'src/app/shared/interfaces/LocalFileDTO';
import { CameraUtils } from 'src/app/shared/utils/CameraUtils';
import { LoaderService } from 'src/app/shared/services/app-loader/loader.service';
import { CreateResumoOperadoraDTO } from 'src/app/shared/interfaces/transacoes/CreateTransacaoDTO';

@Component({
  selector: 'app-fluxo-onze',
  templateUrl: './fluxo-onze.page.html',
  styleUrls: ['./fluxo-onze.page.scss'],
})
export class FluxoOnzePage implements OnInit, OnDestroy {

  @ViewChild('fileInput')
  fileInput: ElementRef

  fileSelected: string;

  form: FormGroup;
  loggedUsers: UserDTO[] = [];

  currentTheme: string;

  steps: MenuItem[];
  stepIndex = 0;

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private funcionarioService: FuncionarioService,
    private storage: StorageService,
    private toastService: MessageService,
    private transacaoService: TransacaoService,
    private preferenceService: PreferencesService,
    private menu: MenuController,
    private platform: Platform,
    private loaderService: LoaderService,

  ) {
    this.form = this.formBuilder.group({
      nome: ['', []],
      user_final_id: [, [Validators.required,]],
      anexo: [, [Validators.required,]],

      valor_transacao_jogos: [, [Validators.required,]],
      quantidade_jogos: [, [Validators.required,]],

      valor_transacao_contas: [, [Validators.required,]],
      quantidade_contas: [, [Validators.required,]],

      valor_transacao_premios: [, [Validators.required,]],
      quantidade_premios: [, [Validators.required,]],

      valor_transacao_pagamentos: [, [Validators.required,]],
      quantidade_pagamentos: [, [Validators.required,]],

      valor_transacao_depositos: [, [Validators.required,]],
      quantidade_depositos: [, [Validators.required,]],

      quantidade_consultas: [, [Validators.required,]],

      valor_transacao_estornos: [, [Validators.required,]],
      quantidade_estornos: [, [Validators.required,]],

      valor_transacao_saques: [, [Validators.required,]],
      quantidade_saques: [, [Validators.required,]],

      valor_saque_bb: [, [Validators.required,]],
      quantidade_saque_bb: [, [Validators.required,]],
      valor_saque_emergencial: [, [Validators.required,]],
      quantidade_saque_emergencial: [, [Validators.required,]],
      valor_estorno_jogo: [, [Validators.required,]],
      quantidade_estorno_jogo: [, [Validators.required,]],
      valor_entrada_extra: [, [Validators.required,]],

    });

    this.steps = [
      {
        label: 'Básico',
      },
      {
        label: 'Resumo',
      },
      {
        label: 'Anexo',
      },
    ];
    this.stepIndex = 0;
  }

  ngOnInit() {
    console.log(this.stepIndex);
    this.loadCurrentLoggedUsers();
    this.currentTheme = this.preferenceService.getThemePreference();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  ionViewWillEnter() {
    this.menu.enable(false);
  }

  ionViewWillLeave() {
    this.menu.enable(true);
  }

  prevStep() {
    if (this.stepIndex > 0)
      this.stepIndex--
  }

  nextStep() {
    if (this.stepIndex < 2)
      this.stepIndex++;
  }

  loadCurrentLoggedUsers() {
    const { loterica_id } = this.storage.getLocalUser().user;
    const { id } = this.storage.getLocalUser().user;
    const sub = this.funcionarioService.listAllLogedUsers(loterica_id)
      .subscribe(loggedUsers => {
        this.loggedUsers = loggedUsers.filter(lu => lu.id !== id);
      });

    this.subscriptions.add(sub);
  }

  createTransacao() {
    const { loterica_id, nome } = this.storage.getLocalUser().user;
    const { id: user_final_id, nome: userSolicitado } = this.form.get('user_final_id').value;

    let nomeTransacao: string = this.form.get('nome').value;
    if (!nomeTransacao || nomeTransacao.trim().length <= 0) {
      const date = new Date();
      nomeTransacao = `Resumo Operadora - de ${nome} para ${userSolicitado} às ${date.toLocaleString("pt-BR")}`;
    }

    const resumoOperadora: CreateResumoOperadoraDTO = {
      nome: nomeTransacao.trim(),
      loterica_id,
      user_final_id,
      quantidade_contas: this.form.get('quantidade_contas').value,
      valor_transacao_contas: this.form.get('valor_transacao_contas').value,
      quantidade_jogos: this.form.get('quantidade_jogos').value,
      valor_transacao_jogos: this.form.get('valor_transacao_jogos').value,
      quantidade_depositos: this.form.get('quantidade_depositos').value,
      valor_transacao_depositos: this.form.get('valor_transacao_depositos').value,
      quantidade_pagamentos: this.form.get('quantidade_pagamentos').value,
      valor_transacao_pagamentos: this.form.get('valor_transacao_pagamentos').value,
      quantidade_estornos: this.form.get('quantidade_estornos').value,
      valor_transacao_estornos: this.form.get('valor_transacao_estornos').value,
      quantidade_premios: this.form.get('quantidade_premios').value,
      valor_transacao_premios: this.form.get('valor_transacao_premios').value,
      quantidade_consultas: this.form.get('quantidade_consultas').value,
      valor_transacao_saques: this.form.get('valor_transacao_saques').value,
      quantidade_saques: this.form.get('quantidade_saques').value,
      valor_saque_bb: this.form.get('valor_saque_bb').value,
      quantidade_saque_bb: this.form.get('quantidade_saque_bb').value,
      valor_saque_emergencial: this.form.get('valor_saque_emergencial').value,
      quantidade_saque_emergencial: this.form.get('quantidade_saque_emergencial').value,
      valor_estorno_jogo: this.form.get('valor_estorno_jogo').value,
      quantidade_estorno_jogo: this.form.get('quantidade_estorno_jogo').value,
      valor_entrada_extra: this.form.get('valor_entrada_extra').value,
    }

    const sub = this.transacaoService.createResumoOperadora(resumoOperadora)
      .subscribe(data => {
        const { transacoes_id } = data;

        const sub = this.transacaoService.insertAnexo(this.form.get('anexo').value)
          .subscribe(data => {
            const { file_id } = data;

            const sub = this.transacaoService.associateAnexoTransacoes({ file_id, transacoes_id })
              .subscribe(data => {
                console.log(data);
                this.presentToast('success', 'Sucesso!', 'Sua transação foi criada com sucesso', Toast.mediumDuration);
                this.router.navigateByUrl('home');
              });

            this.subscriptions.add(sub);
          });
        this.subscriptions.add(sub);
      });
    this.subscriptions.add(sub);
  }

  goBack() {
    this.router.navigateByUrl('home');
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }


  disableSetp1(): boolean {
    const situacao = !(this.form.get('user_final_id').valid && this.form.get('anexo').valid);
    return situacao;
  }

  disableSetp2(): boolean {
    const situacao = !(
      this.form.get('valor_transacao_jogos').valid && this.form.get('quantidade_jogos').valid
      && this.form.get('valor_transacao_contas').valid && this.form.get('quantidade_contas').valid
      && this.form.get('valor_transacao_premios').valid && this.form.get('quantidade_premios').valid
      && this.form.get('valor_transacao_pagamentos').valid && this.form.get('quantidade_pagamentos').valid
      && this.form.get('valor_transacao_depositos').valid && this.form.get('quantidade_depositos').valid
      && this.form.get('quantidade_consultas').valid
      && this.form.get('valor_transacao_estornos').valid && this.form.get('quantidade_estornos').valid);

    return situacao;
  }

  disableSetp3(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

  questionTerminal() {
    this.presentToast('warn', 'Terminal do caixa', 'Insira o terminal que você está operando', Toast.mediumDuration);
  }

  getFileButtonFill() {
    if (this.currentTheme == 'dark') {
      return 'outline';
    }

    return 'solid';
  }


  uploadFile(event) {
    const file = event.target.files[0];
    this.fileSelected = `${file.name} - ${(file.size / 1000).toFixed(2)}KB`;

    this.form.get('anexo').setValue(file);
  }

  async takePhoto() {
    const image = await Camera.getPhoto({
      quality: 100,
      allowEditing: false,
      resultType: CameraResultType.Uri,
      source: CameraSource.Camera
    });

    if (image) {
      this.loaderService.isLoading.next(true);
      try {
        await this.saveImage(image);
      } catch (e) {
        console.error(e.message)
        this.presentToast('error', 'Falha ao salvar a foto', e.message, Toast.mediumDuration);
      }
      this.loaderService.isLoading.next(false);
    }
  }

  async saveImage(photo: Photo) {
    const base64Data = await CameraUtils.readAsBase64(photo, this.platform);
    const fileName = new Date().getTime() + '.png';

    try {
      await Filesystem.mkdir({
        directory: Directory.Data,
        path: `${IMAGE_DIR}`,
        recursive: true,
      });
    } catch (e) {
      console.error('Pasta não criada: ' + e.message);
    }

    await Filesystem.writeFile({
      directory: Directory.Data,
      path: `${IMAGE_DIR}/${fileName}`,
      data: base64Data,
    });

    const readFile = await Filesystem.readFile({
      path: `${IMAGE_DIR}/${fileName}`,
      directory: Directory.Data,
    });

    const localPhoto: LocalFileDTO = {
      data: `data:image/png;base64,${readFile.data}`,
      name: fileName,
      path: `${IMAGE_DIR}/${fileName}`
    }

    const response = await fetch(localPhoto.data);
    const blob = await response.blob();

    const file = new File([blob], fileName);

    this.fileSelected = `${fileName} - ${(blob.size / 1000).toFixed(2)}KB`;
    this.form.get('anexo').setValue(file);
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}
